.contact {
  margin-bottom: 220px;
  font-family: 'Julius Sans One', sans-serif;
}

.contact__in {
  display: flex;
  padding: 40px;
  background-color: #FFF;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.contact__photo {
  display: flex;
  width: 420px;
  margin-right: 80px;
}

.contact__photo img {
  width: 100%;
  border-radius: 5px;
  box-shadow: 0px 2px 10px -5px #000000;
}

.contact__name {
  font-size: 24px;
  letter-spacing: 10px;
  margin-bottom: 20px;
}

.contact__subtitle {
  font-weight: 700;
  font-size: 14px;
  letter-spacing: 2px;
  margin-bottom: 10px;
}

.contact__phone {
  display: flex;
  align-items: center;
  color: #4c4c4c;
  text-decoration: none;
  font-family: 'Julius Sans One', sans-serif;
  font-size: 20px;
  font-weight: 700;
}

.contact__phone:hover svg {
  transform: scale(1.2);
}

.contact__location {
  display: flex;
  align-items: center;
  font-weight: 700;
  cursor: pointer;
}

.contact__location:hover svg {
  transform: scale(1.2);
}

.contact__inner {
  display: flex;
  align-items: flex-end;
  margin-bottom: 20px;
}

.contact__inner label {
  width: 50%;
}

.contact__wrap {
  margin-right: 40px;
}

.contact svg {
  width: 34px;
  height: 34px;
  margin-right: 20px;
  transition: transform 0.4s ease;
}

.contact__form {
  display: flex;
  flex-direction: column;
}

.contact__form label {
  display: flex;
  flex-direction: column;
}

.contact__form label:first-child {
  margin-right: 20px;
}

.contact__form label span {
  font-size: 14px;
  font-weight: 700;
  margin-bottom: 5px;
}

.contact__form input {
  padding: 10px 20px;
  border: 2px solid #009fe8;
  border-radius: 5px;
  font-size: 18px;
  font-family: 'Julius Sans One', 'Jura', sans-serif;
  font-weight: 700;
}

.contact__form textarea {
  resize: none;
  padding: 10px 20px;
  border: 2px solid #009fe8;
  border-radius: 5px;
  font-family: 'Julius Sans One', 'Jura', sans-serif;
  font-weight: 700;
  height: 180px;
  margin-bottom: 20px;
}

.contact__form button.btn {
  position: relative;
  margin-left: auto;
  padding: 15px 30px;
  font-size: 20px;
  font-family: 'Julius Sans One', sans-serif;
  font-weight: 700;
  background-color: #009fe8;
  border-radius: 5px;
  color: white;
  overflow: hidden;
}

.contact__form button.btn:after {
  background: #fff;
  content: '';
  height: 155px;
  left: -75px;
  opacity: .2;
  position: absolute;
  top: -50px;
  transform: rotate(35deg);
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
  width: 50px;
}

.contact__form button.btn:hover {
  color: gold;
}

.contact__form button.btn:hover:after {
  left: 120%;
  transition: all 550ms cubic-bezier(0.19, 1, 0.22, 1);
}

@media only screen and (max-width: 1200px) {
  .contact {
    margin-bottom: 60px;
  }
  .contact__in {
    box-shadow: none;
  }
  .contact__photo {
    display: none;
  }
  .contact__col {
    width: 100%;
  }
  .contact__name {
    text-align: center;
  }
  .contact__inner {
    justify-content: space-between;
  }
  .contact input, .contact textarea {
    background-color: transparent;
  }
}

@media only screen and (max-width: 840px) {
  .contact__inner {
    flex-direction: column;
  }
  .contact__inner label {
    width: 100%;
    margin: 0 0 20px 0;
  }
  .contact__inner label:first-child {
    margin-right: 0;
  }
  .contact__wrap {
    width: 100%;
    margin: 0 0 20px 0;
  }
  .contact__location {
    font-size: 14px;
  }
  .contact__phone {
    font-size: 16px;
  }
}

@media only screen and (max-width: 480px) {
  .contact__in {
    padding: 0;
    background-color: transparent;
  }
  .contact input, .contact textarea {
    border-color: #a0e1ff;
  }
}

.nav {
  display: flex;
}

.nav__list {
  display: flex;
  align-items: center;
}

.nav__list li {
  display: flex;
  margin-right: 80px;
}

.nav__list a {
  position: relative;
  font-family: 'Julius Sans One', sans-serif;
  font-size: 14px;
  font-weight: 700;
  letter-spacing: 2px;
  color: white;
  text-decoration: none;
  margin-top: -80px;
  opacity: 0;
  overflow: hidden;
}

.nav__list a:before {
  content: '';
  position: absolute;
  transition: transform .5s ease;
  left: 0;
  bottom: 0;
  width: 100%;
  height: 2px;
  background-color: gold;
  transform: translateX(-110%);
}

.nav__list a:hover {
  color: gold;
}

.nav__list a:hover:before {
  transform: translateX(0);
}

@media only screen and (max-width: 1024px) {
  .nav__list {
    flex-direction: column;
    margin: 100px auto 0;
  }
  .nav__list a {
    font-size: 20px;
  }
  .nav__list li {
    margin-bottom: 30px;
    margin-right: 0;
  }
}

