.picture {
  display: flex;
  flex-direction: column;
  border-radius: 5px;
  border: 1px solid rgba(0, 0, 0, 0.2);
  box-shadow: 0px 1px 4px rgba(0, 0, 0, 0.15);
  padding: 10px;
  color: #4c4c4c;
  background-color: #fff;
  text-decoration: none;
